.card {
  background: #fff;
  border-radius: 2px;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  background:black;
}
.card > .img-container > img {
  height: 75%;
  width: 75%;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  text-align: center;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  color: black;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

.scoreSummary{
  width: 100%;
  text-align: center;
  margin-bottom: 20px;	
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

